import React from 'react';
import sql from 'mssql';

import ListasSeleVariable from '../../ListasSeleVariable.js';
import CrearFuenteDatosAtributos from './CrearFuenteDatosAtributos.js';

var nombreCampoSeleccionado = '';
const tipoCampos = [ {nombre: "texto"}, {nombre: "booleano"}, {nombre: "fecha"}, {nombre: "número"}, {nombre: "arreglo"}];

export default class CrearFuenteDatos extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            atributos: [],
            mostrarEsObjeto: true,
            titulo: "Valores Multiples"
        }
        this.crearFuenteDato = this.crearFuenteDato.bind(this);
        this.retornoSeleccionVariable = this.retornoSeleccionVariable.bind(this);
        this.cambioAObjeto = this.cambioAObjeto.bind(this);
        this.cambiarTitulo = this.cambiarTitulo.bind(this);
    }

    crearFuenteDato () {
        var nombre = $("#nombreFuenteDato").val();
        var tipo = $("#tipoFuenteDato").val();
        var guardar;
        if ($("#guardarFuenteDato").is(':checked'))
            guardar = true;
        else
            guardar = false;
        var formula = nombreCampoSeleccionado;
        var nivel = 0;
        console.log('this.props.idTablaSeleccionada');
        console.log(this.props.idTablaSeleccionada);
        console.log('nombre');
        console.log(nombre);
        console.log('tipo');
        console.log(tipo);
        console.log('guardar');
        console.log(guardar);
        console.log('formula');
        console.log(formula);
        const transaction = new sql.Transaction( this.props.pool );
        transaction.begin(err => {
            var rolledBack = false;
            transaction.on('rollback', aborted => {
                rolledBack = true;
            });
            const request = new sql.Request(transaction);
            request.query("insert into Campos (tablaID, nombre, tipo, guardar, formula, nivel) values ("+this.props.idTablaSeleccionada+", '"+nombre+"', '"+tipo+"', '"+guardar+"', '"+formula+"', "+nivel+")", (err, result) => {
                if (err) {
                    if (!rolledBack) {
                        console.log(err);
                        transaction.rollback(err => {
                        });
                    }
                } else {
                    transaction.commit(err => {
                        this.props.terminoCrearCampo(nombre);
                    });
                }
            });
        }); // fin transaction
    }

    retornoSeleccionVariable (variable) {
        nombreCampoSeleccionado = '';
        if(variable[0].nombre.length > 0) {
            nombreCampoSeleccionado = variable[0].nombre;
        }
    }

    cambioAObjeto () {
        this.setState({
            mostrarEsObjeto: !this.state.mostrarEsObjeto
        }, this.cambiarTitulo );
    }

    cambiarTitulo () {
        if(this.state.mostrarEsObjeto) {
            this.setState({
                titulo: "Valores Multiples"
            });
        } else  {
            this.setState({
                titulo: "Valor Único"
            });
        }
    }

    render() {
        return (
            <div>
                <div className={"row"}>
                    <div className={"col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12"}>
                        <div className={"page-header"}>
                            <h2 className={"pageheader-title"}>Crear Riesgo</h2>
                            <div className={"page-breadcrumb"}>
                                <nav aria-label="breadcrumb">
                                    <ol className={"breadcrumb"}>
                                        <li className={"breadcrumb-item font-16"} aria-current="page" onClick={this.props.configuracionHome}><a href="#" className={"breadcrumb-link"}>Configuraci&oacute;n</a></li>
                                        <li className={"breadcrumb-item active font-16"} aria-current="page">Crear Fuente de Datos</li>
                                    </ol>
                                </nav>
                            </div>
                        </div>
                    </div>
                </div>
                <div className={"row"}>
                    <div className={"col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12"}>
                        <div className={"card influencer-profile-data"}>
                            <div className={"card-body"}>
                                <div className={"border-top border-bottom addPaddingToConfig"} style={{width: "100%"}}>
                                    <div className={"row"} style={{width: "100%"}}>
                                        <div className={"col-xl-3 col-lg-3 col-md-3 col-sm-3 col-3 form-group"}>
                                            <label htmlFor="nombreFuenteDato" className="col-form-label">Nombre Fuente de Dato (Variable Origen)</label>
                                        </div>
                                        <div className={"col-xl-9 col-lg-9 col-md-9 col-sm-9 col-9 form-group"}>
                                            <input id="nombreFuenteDato" type="text" className="form-control form-control-sm"/>
                                        </div>
                                    </div>
                                    <div className={"row"} style={{width: "100%"}}>
                                        <div className={"col-xl-3 col-lg-3 col-md-3 col-sm-3 col-3 form-group"}>
                                            <label htmlFor="esObjetoFuenteDato" className="col-form-label">Tiene más de un atributo / propiedad:</label>
                                        </div>
                                        <div className={"col-xl-9 col-lg-9 col-md-9 col-sm-9 col-9 form-group"}>
                                            <div className={"switch-button-variable switch-button-yesno"} style={{margin:"0 auto", display:"block"}}>
                                                <input type="checkbox" defaultChecked name={"esObjetoFuenteDato"} id={"esObjetoFuenteDato"} onClick={this.cambioAObjeto}/><span>
                                                <label htmlFor={"esObjetoFuenteDato"}></label></span>
                                            </div>
                                        </div>
                                    </div>
                                    <div className={"row"} style={{width: "100%"}}>
                                        <div className={"col-xl-3 col-lg-3 col-md-3 col-sm-3 col-3 form-group"}>
                                            <label htmlFor="guardarFuenteDato" className="col-form-label">Guardar Valores Obtenidos en Base de Datos</label>
                                        </div>
                                        <div className={"col-xl-9 col-lg-9 col-md-9 col-sm-9 col-9 form-group"}>
                                            <br/>
                                            <div className={"switch-button switch-button-yesno"} style={{margin:"0 auto", display:"block"}}>
                                                <input type="checkbox" defaultChecked name={"guardarFuenteDato"} id={"guardarFuenteDato"}/><span>
                                                <label htmlFor={"guardarFuenteDato"}></label></span>
                                            </div>
                                        </div>
                                    </div>
                                    <div className={"row"} style={{width: "100%"}}>
                                        <div className={"col-xl-3 col-lg-3 col-md-3 col-sm-3 col-3 form-group"}>
                                            <label htmlFor="tipoFuenteDato" className="col-form-label">Tipo de Variable</label>
                                        </div>
                                        <div className={"col-xl-9 col-lg-9 col-md-9 col-sm-9 col-9 form-group"}>
                                            <select id="tipoFuenteDato" className="form-control">
                                                {tipoCampos.map((tipo, i) =>
                                                    <option value={tipo.nombre} key={tipo.nombre}>{tipo.nombre}</option>
                                                )}
                                            </select>
                                        </div>
                                    </div>
                                    <br/>
                                    <div className={"row"} style={{width: "100%", border: "1px solid #e6e6f2"}}>
                                        <CrearFuenteDatosAtributos atributos={this.state.atributos} campos={this.props.columnas} titulo={this.state.titulo} mostrarEsObjeto={this.state.mostrarEsObjeto} terminarCrearAtributo={this.crearAtributoDeObjeto}> </CrearFuenteDatosAtributos>
                                    </div>
                                    <br/>
                                    { !this.state.mostrarEsObjeto ? (
                                        <div>
                                            <div className={"row"} style={{display: "flex", alignItems: "center", justifyContent: "center"}}>
                                                <a className={"btn btn-primary btnWhiteColorHover font-bold font-20"} style={{color: "#fafafa"}} onClick={this.crearFuenteDato}>Crear</a>
                                            </div>
                                            <br/>
                                        </div>
                                    ) : (
                                        null
                                    )}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
